  font-weight: bold;
  padding: 0.9375rem 2.4375rem;
  line-height: 1.571;
  position: relative;
  text-decoration: none;
  text-align: justify;
  transition: 0.3s ease-in-out;
  width: 12.625rem;
  margin-bottom: 0.3rem;
  z-index: 10;
}

.btn.btn--white {
  background-color: #fff;
  color: #408f95;
  border: 1px solid #fff;
}

.btn::after {
  content: "";
  position: absolute;
  top: 2%;
  left: 2%;
  width: 101%;
  height: 107%;
  border: 1px solid #408f95;
  transition: 0.2s;
  z-index: -2;
}

.btn.btn--white::after {
  border: 1px solid #fff;
}

.btn span::before {
  mask-image: url(../images/common/btn-arow.svg);
  mask-repeat: no-repeat;
  background-color: #fff;
  position: absolute;
  content: "";
  top: 27%;
  right: -115%;
  width: 100%;
  height: 1rem;
  z-index: 3;
  display: inline-block;
}

.btn.btn--white span::before {
  background-color: #408f95;
}

.btn:hover span::before {
  background-color: #408f95;
}

.btn.btn--white:hover span::before {
  background-color: #fff;
}

.btn:hover {
  stroke: #408f95;
  z-index: 4;
}

.btn.btn--white:hover {
  stroke: #fff;
}

/* ボタン内spanの形状 */
.btn span {
  position: relative;
  z-index: 3;
  font-size: 0.875rem;
  font-weight: 300;
  font-family: "Gotu", sans-serif;
  line-height: 1.57;
  color: #fff;
}

