  color: #408f95;
}

.btn:hover span {
  color: #408f95;
}

.btn.btn--white:hover span {
  color: #fff;
}

/*== 背景が流れる（左から右） */
.btn:before {
  content: "";
  /*絶対配置で位置を指定*/
  position: absolute;
  top: 0;
  left: 0;
  /*色や形状*/
  background: #fff; /*背景色*/
  width: 100%;
  height: 100%;
  /*アニメーション*/
  transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1) 0s;
  transform: scale(0, 1);
  transform-origin: right top;
}

.btn.btn--white::before {
  background-color: #408f95;
}

.btn:hover:before {
  transform-origin: left top;
  transform: scale(1, 1);
}

